import React from 'react';
import styled from 'styled-components';
import { Controller, Scene } from 'react-scrollmagic';
import gsap from 'gsap';
import { Tween, Timeline } from 'react-gsap';

const SectionWipes2Styled = styled.div`


  overflow: hidden;
  #pinContainer {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
  }
  #pinContainer .panel {
    height: 100vh;
    width: 100vw;
    position: absolute;
    text-align: center;
  }
  .panel span {
    position: relative;
    display: block;
   border: 5px solid black;
   margin:100px;
    font-size: 60px;
  }
  
  .panel.white {
    background-color: white;
  }
  
`;

const Home = () => (
  <SectionWipes2Styled>
    <Controller>
      <Scene
        triggerHook="onLeave"
        duration="300%"
        pin
      >
        <Timeline
          wrapper={<div id="pinContainer" />}
        >
          <section className="panel white">
              
              <span> built with love</span></section>
          <Tween
            from={{ y: '100%' }}
            to={{ y: '0%' }}
          >



            <section className="panel white">
                
                <span>with passion</span></section>
          </Tween>
          <Tween
            from={{ y: '100%' }}
            to={{ y: '0%' }}
          >



            <section className="panel white">
                
                <span>and hate</span></section>
          </Tween>


          <Tween
            from={{ y: '100%' }}
            to={{ y: '0%' }}
          >


            <section className="panel white">
                 <span>enjoy!</span>
            </section>
          </Tween>

          <Tween
            from={{ y: '100%' }}
            to={{ y: '0%' }}
          >


           
              </Tween>
    
         

        
        </Timeline>
      </Scene>
    </Controller>
  </SectionWipes2Styled>
);

export default Home;