import React from 'react';
import Details from '../components/Details'
import styles from './Container.module.css'
import me from './images/me.jpg'
import coffee from './images/coffee.jpg'

class Container extends React.Component{
    constructor(props){
        super();
        this.state={
            val:1
        }
        this.setVal=this.setVal.bind(this);
    
    }
    setVal = (x) =>{
        this.setState( state =>{
            return {val:x}
    })
}
    
    render(){
        return (            
            <div style={{marginTop:'40px'}}>



<nav class="navbar navbar-expand-lg navbar-light " style={{backgroundColor: '#fff', position:'relative',
fontFamily: 'Lacquer', fontSize:'25px'}}>
      <div class="nav-item" ><img src={me} style={{width:'85px', height:'70px'}} />  </div>
       

          <ul class="navbar-nav mr-auto">
            <li class="nav-item active">
              <p className={styles.cng} onClick={() => this.setVal(1)}>About Me &nbsp;</p>
            </li>
            <li class="nav-item active">
              <p className={styles.cng} onClick={() => this.setVal(2)}>Portfolio &nbsp;</p>
            </li>
      
            <li class="nav-item active">
                  <p className={styles.cng} onClick={() => this.setVal(3)}>Gallery</p>
                </li>
      
              </ul>
                <ul class="nav navbar-nav navbar-right">

                  <li class="nav-item active">
                    <a class="nav-link" href="https://github.com/alex-rgb-25?tab=repositories"><i class="fab fa-github"></i></a>
                  </li>
      
                <li class="nav-item active">
                    <a class="nav-link" href="https://www.linkedin.com/in/constantin-alexandru-simionescu-61347b191/"><i class="fab fa-linkedin"></i></a>
                  </li>
      
      
                  <li class="nav-item active popup" >
                      <p class="nav-link">
                        <i class="fas fa-envelope"></i></p>
                    </li>
      
              
                    </ul>
          
        <div class="nav-item" ><img src={coffee}  style={{ width: '85px', height: '70px'}} />  </div>
      
      </nav>


                <Details val = {this.state.val}/>

</div>
        )
    }
}


export default Container;