import React from 'react';
import logo1 from './images/cat.png'
import logo2 from './images/stars.png'
import logo3 from './images/rest_blog.png'
import logo4 from './images/yelp.png'
import logo5 from './images/song.png'
import logo6 from './images/todos.png'
import logo7 from './images/mongo.png'
import logo8 from './images/patatap.png'
import logo9 from './images/color.png'
import About from './About'
import { ParallaxProvider } from 'react-scroll-parallax';
import SectionWipes2 from './Gallery'
import classes from './Details.module.css'
import Home from './Home'


class Details extends React.Component{
    constructor(props){
        super();
        this.state={
            index:3
        }
        this.handleIndex1 = this.handleIndex1.bind(this);
        this.handleIndex2 = this.handleIndex2.bind(this);
    }
    handleIndex1 = () => {
        if(this.state.index==7){
            this.setState( state =>{
                return {index:0}
            })
        }else{
                this.setState( state =>{
                    return {index: this.state.index+1}
                })
            }

    }

    handleIndex2 = () => {
        if(this.state.index==0){
            this.setState( state =>{
                return {index:7}
            })
        }else{
                this.setState( state =>{
                    return {index: this.state.index-1}
                })
            }

    }

    render(){


        const pol2 = {
            position: 'absolute',
            right:'0px',
            margin:'0',
            top:'50%',
            msTransform: 'translateY(-50%)',
            transform: 'translateY(-50%)',
            width: '0',
            height: '0',
            borderLeft: '100px solid rgb(203, 212, 114)',
            borderTop: '50px solid transparent',
            borderBottom: '50px solid transparent',
            opacity: '90%',
            float:'right'
        }


const pol1 = {
    position: 'absolute',
    top: '50%',
    msTransform: 'translateY(-50%)',
    transform: 'translateY(-50%)',
    width: '0',
    height: '0',
    borderRight: '100px solid rgb(203, 212, 114)',
    borderTop: '50px solid transparent',
    borderBottom: '50px solid transparent',
    opacity: '90%',
    float:'left'
}

const inner = {
    width:'100%', 
    display:'block', 
    margin: '0 auto',
    position: 'relative',
    height:'100%',
    background: 'gray'
   
}











const projs =[logo1,logo2, logo3, logo4, logo5,logo6,logo7,logo8]
const urls = ["https://fathomless-castle-85138.herokuapp.com/", "https://mighty-ocean-37051.herokuapp.com/","https://shielded-wildwood-32656.herokuapp.com/blogs",
"https://arcane-meadow-43995.herokuapp.com/","https://quiet-mesa-41787.herokuapp.com/","https://fast-savannah-37925.herokuapp.com/",
"https://calm-lake-56018.herokuapp.com/","https://desolate-savannah-52198.herokuapp.com/"]

        if(this.props.val==1){
            return (
               <div>
                <About />
                <Home />
                </div>
            )
        } else if(this.props.val==2){
            return (
                <div className={classes.blk}>


<div class = "container" style = {{background:'rgb(0, 0, 0)', borderRadius: '8px'}}>
<div>
        <p style = {{color: 'rgb(207, 207, 207)',  fontWeight:'540'}}>
           I've started studying web development on my own.

               Skills: html, css, bootstrap, javascript, jQuery, NodeJS, working with databases - MongoDB and a bit of MySQL. 
                I'm very excited to expand my knowledge.</p> <p style ={{color: 'rgb(207, 207, 207)',
                 fontWeight: '540'}}>I am currently looking into Angular and React courses.
                    I also have a bit of background with studying in college (2 years in Computer Science) that exposed me to different areas.
                    I'm also learning some Java on the side.
                    I mostly follow courses on Udemy and freeCodeCamp and try to fill whatever else I need from different sources.
                    </p>



                    <p style ={{color: 'rgb(207, 207, 207)',  fontWeight:'540'}}>
                    Check out more stuff on github: <a href="https://github.com/alex-rgb-25?tab=repositories"><i class="fab fa-github"></i></a> </p>
                </div>
              </div>


<div style ={{margin:'60px', }} >

                    <div style ={inner}  >
                        
                        <a href={urls[this.state.index]}><img src={projs[this.state.index]}  style ={{width:'100%', display:'block', 
margin: '0 auto', border:'7px solid black', boxShadow: '38px -24px 3px -2px rgba(36,36,36,1)'}} /></a>
                         <div style={pol1}  onClick={()=> this.handleIndex2()}></div>
                         <div style={pol2} onClick={()=> this.handleIndex1()} ></div>
                        </div>
                    
                    </div>
                <br/>
                <br/>
                    <div style={{backgroundColor: 'rgb(0, 0, 0)', paddingTop:'30px'}}>
<div class = "row">


      <div class="col-lg-6 col-sm-6"  >
          <a href= "https://fathomless-castle-85138.herokuapp.com/"><img src={logo1}
          style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
        </div>

        <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href="https://mighty-ocean-37051.herokuapp.com/"><img src={logo2} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>

    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href="https://shielded-wildwood-32656.herokuapp.com/blogs"><img src={logo3} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>

    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href="https://arcane-meadow-43995.herokuapp.com/"><img src={logo4} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>

    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href="https://quiet-mesa-41787.herokuapp.com/"><img src={logo5} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>

    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href="https://fast-savannah-37925.herokuapp.com/"><img src={logo6} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>

    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href="https://calm-lake-56018.herokuapp.com/"><img src={logo7} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>


    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href= "https://desolate-savannah-52198.herokuapp.com/"><img src={logo8} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>

    <div class="col-lg-6 col-sm-6" style={{paddingBottom: '30px'}}>
          <a href= "https://secret-bastion-53095.herokuapp.com/"><img src={logo9} style={{border: '2px solid rgb(210, 198, 198)',
            borderRadius:'5px'}} class="card-img-top" alt="..." /></a>
    </div>


  </div>
    </div>

            </div>
            )
        } else if(this.props.val==3){
            return (
                <div>
                    <h2 style={{textAlign:'center'}}>Scroll <i className={classes.arrow}></i></h2>
                <SectionWipes2 />
                </div>
            )
        }

    }
}


export default Details;