
// @flow
import React from 'react';
import styled from 'styled-components';
import { Controller, Scene } from 'react-scrollmagic';
import gsap from 'gsap';
import { Tween, Timeline } from 'react-gsap';
import i3 from './images/gallery/IMG_1357.JPG'
import i2 from './images/gallery/IMG_2185.JPG'
import i1 from './images/gallery/IMG_2303.JPG'
import i4 from './images/gallery/IMG_2439.JPG'
import i5 from './images/gallery/IMG_2433.JPG'
import i6 from './images/gallery/IMG_2429.JPG'
import i7 from './images/gallery/IMG_2428.JPG'


const SectionWipes2Styled = styled.div`

@media (max-width: 500px) {
    /* CSS that should be displayed if width is equal to or less than 800px goes here */
    img{
        width:100%;
    }
  }
  @media screen and (min-width: 500px) and (max-width: 700px) { 
    img{
        width:70%;
    }
}

@media screen and (min-width: 700px) and (max-width: 1000px) { 
    img{
        width:45%;
    }
}

  @media (min-width: 1000px) {
    /* CSS that should be displayed if width is equal to or less than 800px goes here */
    img{
        width:35.4%;
    }
  }





  overflow: hidden;
  #pinContainer {
    height: auto;
    width: 100%;
    overflow: hidden;
  }
  #pinContainer .panel {
    height: auto;
    width: 100%;
    position: absolute;
    text-align: center;
  }
  .panel span {
    position: relative;
    display: block;
   
    font-size: 80px;
  }
  
  .panel.blue {
    background-color: white;
  }
  
  .panel.turqoise {
    background-color: black;
  }
  
  .panel.green {
    background-color: white;
  }
  
  .panel.bordeaux {
    background-color: black;
  }
`;

const SectionWipes2 = () => (
  <SectionWipes2Styled>
    <Controller>
      <Scene
        triggerHook="onLeave"
        duration="300%"
        pin
      >
        <Timeline
          wrapper={<div id="pinContainer" />}
        >
          <section className="panel blue">
              
              
          <img src={i1} style={{border: '10px solid black',
            borderRadius:'5px', }} alt="..." />
              
              <span> </span></section>
          <Tween
            from={{ x: '-100%' }}
            to={{ x: '0%' }}
          >



            <section className="panel turqoise">
            <img src={i2} style={{border: '10px solid white',
            borderRadius:'5px'}} alt="..." />
                
                <span></span></section>
          </Tween>
          <Tween
            from={{ x: '100%' }}
            to={{ x: '0%' }}
          >



            <section className="panel green">
                
            <img src={i3} style={{border: '10px solid black',
            borderRadius:'5px'}} alt="..." />
                <span></span></section>
          </Tween>


          <Tween
            from={{ y: '-100%' }}
            to={{ y: '0%' }}
          >


            <section className="panel bordeaux">
            <img src={i4} style={{border: '10px solid white',
            borderRadius:'5px'}} alt="..." />
                 <span></span>
            </section>
          </Tween>

          <Tween
            from={{ x: '-100%' }}
            to={{ x: '0%' }}
          >


            <section className="panel blue">
              
              
              <img src={i5} style={{border: '10px solid black',
                borderRadius:'5px', }} alt="..." />
                  
                  <span> </span></section>
                  </Tween>
              <Tween
                from={{ y: '-100%' }}
                to={{ y: '0%' }}
              >

            <section className="panel turqoise">
            <img src={i6} style={{border: '10px solid white',
            borderRadius:'5px'}} alt="..." />
                
                <span></span></section>
          </Tween>



          <Tween
            from={{ x: '-100%' }}
            to={{ x: '0%' }}
          >


<section className="panel green">
                
                <img src={i7} style={{border: '10px solid black',
                borderRadius:'5px'}} alt="..." />
                    <span></span></section>
              </Tween>
    

    
    

              <Tween
            from={{ x: '-100%' }}
            to={{ x: '0%' }}
          >


<section className="panel green">
                
                <img src={i7} style={{border: '10px solid white',
                borderRadius:'5px'}} alt="..." />
                    <span></span></section>
              </Tween>

              

          <Tween
            from={{ x: '-100%' }}
            to={{ x: '0%' }}
          >


<section className="panel green">
                
                <img src={i7} style={{border: '10px solid black',
                borderRadius:'5px'}} alt="..." />
                    <span></span></section>
              </Tween>

              

          <Tween
            from={{ x: '-100%' }}
            to={{ x: '0%' }}
          >


<section className="panel green">
                
                <img src={i7} style={{border: '10px solid white',
                borderRadius:'5px'}} alt="..." />
                    <span></span></section>
              </Tween>
    
         

        
        </Timeline>
      </Scene>
    </Controller>
  </SectionWipes2Styled>
);

export default SectionWipes2;